import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { DialogService } from 'ng2-bootstrap-modal';

/* Components */
import { MessageModalComponent } from '../../modals/message-modal/message-modal.component';
import { NeedModalComponent } from '../../modals/need-modal/need-modal.component';

/* Services */
import { ProjectsService } from '../../../Services/Projects/projects.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';

@Component({
	selector: 'app-project-needs',
	templateUrl: './project-needs.component.html',
	styleUrls: [
		'./project-needs.component.scss',
		'../../../../public/styles/need-card.scss'
	]
})
export class ProjectNeedsComponent implements OnInit, OnChanges {

	@Input() my_project : boolean
	@Input() profile 	: Object
	@Input() project 	: Object
	needs 				: Object[]

	constructor(private dialogService: DialogService,
				private ProjectsService: ProjectsService,
				private PicturesService: PicturesService) { }

	ngOnInit() {
		this.needs 	= this.project['openings'];
	}

	ngOnChanges(changes: SimpleChanges) {
		this.needs 	= this.project['openings'];
	}

	transformTags(tags) {
		if (tags && tags != 0) {
			if (tags.indexOf(','))
				return tags.split(',');
			else
				return JSON.parse(tags);
		} else
			return []
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	/* SHOW MODAL */
	showMessageModal() {
        let disposable 	= this.dialogService.addDialog(MessageModalComponent, {
            profile : this.profile
	    });
	}

	showAddNeedModal() {
		let disposable 	= this.dialogService.addDialog(NeedModalComponent, {
            project_id 	: this.project['id']
	    }).subscribe( need => {
	    	if (need)
	    		this.needs.push(need);
	    });
	}
}



