import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { PipeModule } from '../../Pipes/pipe.module';
import { FooterModule } from '../footer/footer.module';

/* Components */
import { ProjectsComponent } from './projects.component'
import { ProjectNeedsComponent } from './project-needs/project-needs.component';
import { ProjectDiscussionsComponent } from './project-discussions/project-discussions.component';
import { ModalModule } from '../modals/modal.module';

/* Services */
import { ProjectsService } from '../../Services/Projects/projects.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { ProjectDiscussionsService } from '../../Services/Projects/project-discussions.service';
import { ProjectReplyDiscussionService } from '../../Services/Projects/project-reply-discussion.service';
/* Directives */
import { ProjectDiscussionDirective} from '../../Directives/Projects/project-discussion.directive';

/* Librairies */
// import { FroalaEditorModule, FroalaViewModule } from 'angular2-froala-wysiwyg';
import { Ng2PageScrollModule } from 'ng2-page-scroll';
import { MomentModule } from 'angular2-moment';

/* Route */
import { PROJECT_ROUTES } from '../../Routes/projects.routes';

@NgModule({
	imports: [
		PROJECT_ROUTES,
		CommonModule,
		FormsModule,
		RouterModule,
		MomentModule,
		PipeModule,
		FooterModule,
		ModalModule,
		Ng2PageScrollModule.forRoot(),
		// FroalaEditorModule.forRoot(),
  //       FroalaViewModule.forRoot(),
	],
	declarations: [
		ProjectsComponent,
		ProjectNeedsComponent,
		ProjectDiscussionsComponent,
		ProjectDiscussionDirective,
	],
	entryComponents: [
	],
	providers: [
		ProjectsService,
		ProfilesService,
		PicturesService,
		ProjectDiscussionsService,
		ProjectReplyDiscussionService
	]
})
export class ProjectsModule { }



