import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class ProjectReplyDiscussionService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }

	// Get all discussion's replies
	getReplies(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/discussions/' + id + '/replies'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Create a discussion's reply
	createReply(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/discussions/' + id + '/replies'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Update a discussion's reply
	updateReply(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/replies/' + id

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Like a discussion's reply
	likeReply(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/replies/' + id + '/like'

		return this.AuthHttp.post(url, body) // TODO: Do I need body as paramater ? Because POST method need an Object as parameter.
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Unlike a discussion's reply
	unlikeRepley(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/replies/' + id + '/like'

		return this.AuthHttp.delete(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Remove a discussion's reply
	deleteReply(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/replies/' + id

		return this.AuthHttp.delete(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}
}



