import { Component, OnInit, Input, Inject, ViewEncapsulation, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { DOCUMENT, Title } from '@angular/platform-browser';

/* Components */
import { MessageModalComponent } from '../modals/message-modal/message-modal.component';
import { PopoverLoginComponent } from '../modals/popover-login/popover-login.component';

/* Services */
import { ProjectsService } from '../../Services/Projects/projects.service';
import { ProfilesService } from '../../Services/Profiles/profiles.service';
import { TokenService } from '../../Services/Authentication/token.service';
import { PicturesService } from '../../Services/Pictures/pictures.service';
import { MetaService } from '../../Services/meta.service';

/* Interfaces */
import { SearchModel } from '../../Interfaces/search-models';
import { searchProject } from '../../Interfaces/Constants/search-project.constant';

/* Librairies */
import { PageScrollConfig, PageScrollService, PageScrollInstance } from 'ng2-page-scroll';
import { DialogService } from 'ng2-bootstrap-modal';
import { FacebookService, UIParams, UIResponse } from 'ngx-facebook';

declare var videojs : any;

/* Animation */
import { projectAnim, projectAnim2, projectAnim3 } from '../../Animations/animations';

@Component({
	selector: 'app-projects',
	templateUrl: './projects.component.html',
	styleUrls: ['./projects.component.scss'],
	encapsulation: ViewEncapsulation.None,
	animations: [projectAnim, projectAnim2, projectAnim3],
	providers: [
		FacebookService,
		PageScrollService,
	]
})
export class ProjectsComponent implements OnInit, OnDestroy {

	my_project 		: boolean = false
	videoLoad 		: boolean = false
	admin 			: boolean = false
	showPlayer 		: boolean = false
	project_id 		: number
	project 		: any
	similarProjects : Object[]
	searchBody 		: SearchModel
	author 			: Object
	slideState 		: string
	rotateState 	: string
	// buttonText 		: string
	showFilterText 	: boolean = true
	mobile 			: boolean = false
	my_id 			: number
	d_length		: number
	leftPixel 		: string
	videoJSplayer 	: any;

	constructor(private ProjectsService: ProjectsService,
				private ProfilesService: ProfilesService,
				private route: ActivatedRoute,
				private router: Router,
				private TokenService: TokenService,
				private dialogService: DialogService,
				private FB: FacebookService,
				private PicturesService: PicturesService,
				private PageScrollService: PageScrollService,
				private Title: Title,
				private MetaService: MetaService,
				@Inject(DOCUMENT) private document: any) {

		this.initWindow();
		this.slideState 	= 'inactive'
		this.rotateState 	= 'down'

		PageScrollConfig.defaultDuration = 1000;
		PageScrollConfig.defaultScrollOffset = 50;
        PageScrollConfig.defaultEasingLogic = {
            ease: (t: number, b: number, c: number, d: number): number => {
                // easeInOutExpo easing
                if (t === 0) return b;
                if (t === d) return b + c;
                if ((t /= d / 2) < 1) return c / 2 * Math.pow(2, 10 * (t - 1)) + b;
                return c / 2 * (-Math.pow(2, -10 * --t) + 2) + b;
            }
        };
	}

	ngOnInit() {
		this.initModels();
		this.initParams();
		this.reloadOnParamsChange();
	}

	showVideoPlayer() {
		this.showPlayer = true;
		setTimeout( () => {
			this.videoJSplayer = window["videojs"](document.getElementById('my_video'), {}, () => {});
		}, 0)
	}

	ngOnDestroy() {
		if (this.videoJSplayer)
			this.videoJSplayer.dispose();
	}

	reloadOnParamsChange() {
		this.route.params.forEach(params => {
			if (this.videoJSplayer) {
				this.videoJSplayer.dispose();
				this.videoJSplayer = null
			}
			this.showPlayer = false;
            this.initModels();
			this.initParams();
        })
	}

	initWindow() {
		window.scrollTo(0, 0);

		if ((window.screen.width) < 736)
			this.mobile = true
	}

	initModels() {
		this.searchBody		= searchProject
		// this.buttonText 	= 'more'
	}

	initServices(public_id) {
		this.getProject(public_id);
	}

	initParams() {
		this.route.params.subscribe((params: Params) => {
			this.initServices(params['public_id']);
		});

		setTimeout( () => {
			this.route.queryParams.subscribe(params => {
				this.goToDiscussion("#" + params['discussion']);
			});
		}, 1000);
	}

	// initSEO() {
	// 	const url = 'https://www.wittycircle.com' + this.router.url
	// 	this.meta.setTitle(`${this.project['title']} | Wittycircle`);
	// 	this.meta.setTag('og:description', this.project['description'])
	// 	this.meta.setTag('og:image', this.project['picture']);
	// 	this.meta.setTag('og:url', url);
	// }

	setMetaData() {
		const url = 'https://www.wittycircle.com' + this.router.url

		this.Title.setTitle(`${this.project['title']} | Witty`);
		this.MetaService.setMeta('description', this.project['description']);

		this.MetaService.setMeta('og:title', this.project['title'] + ' - Witty');
		this.MetaService.setMeta('og:description', this.project['description']);
		this.MetaService.setMeta('og:url', url);
		this.MetaService.setMeta('og:image', this.project['logo'] || this.project['picture']);
	}

	getProject(public_id) {
		this.ProjectsService.getProject(public_id).subscribe( res => {
			this.project = res.project
			if (!this.project)
				return this.router.navigate(['/welcome']);
			if (this.project.video)
				this.showVideoPlayer()
			this.getPermission(this.project.profile_id);
			this.getAuthorProfile(this.project.profile_id);
			this.searchSimilarProjects();
			this.setMetaData();
		});
	}

	getPermission(profile_id) {
		const token = this.TokenService.getToken();
		if (token) {
			this.my_id 		= token.user.id
			this.my_project = token.user.id === profile_id ? true : false
			this.admin 		= token.user.moderator ? true : false
		}
	}

	getAuthorProfile(profile_id) {
		this.ProfilesService.getProfile(profile_id).subscribe( res => {
			this.author = res.profile
		});
	}

	getAddress(place: Object) {
		console.log(place);
	}

	searchSimilarProjects() {
		this.searchBody.query.members[1].value 	= this.project.category
		this.searchBody.paginate.limit 			= 7
		this.ProjectsService.searchProject(this.searchBody).subscribe( res => {
			this.similarProjects = res.projects
		});
	}

	followProject(id) {
		if (!this.TokenService.getToken())
			return this.showLoginPopOver();

		this.ProjectsService.followProject(id, {}).subscribe( res => {
			if (res.success && res.type === "Like") {
				this.project['follower_count'] += 1
				this.project['hasLiked'] 		= true
			}
			else {
				this.project['follower_count'] -= 1
				this.project['hasLiked'] 		= false
			}
		});
	}

	editProject(public_id) {
		this.router.navigate(['/project/update'], { queryParams: { id: public_id } });
	}

	showText() {
		this.slideState 	= this.slideState 	=== 'inactive' ? 'active' : 'inactive'
		this.rotateState 	= this.rotateState 	=== 'down' ? 'up' : 'down'
		if (this.showFilterText) {
			this.showFilterText = false
		} else {
			setTimeout(() => {
				this.showFilterText = true;
			}, 800)
		}
	}

	shareFacebook() {
		let params: UIParams = {
			method: 'feed',
			name: this.project.title,
			link: 'https://www.wittycircle.com/project/' + this.project.public_id + '/' + this.transformUrl(this.project.title),
			picture: this.project.picture,
			caption: 'Wittycircle.com',
			description: this.project.description
		}
		this.FB.ui(params)
			.then((res: UIResponse) => console.log(res))
			.catch((e: any) => console.error(e));
	}

	goToDiscussion(id): void {
		let pageScrollInstance: PageScrollInstance = PageScrollInstance.simpleInstance(this.document, id);
		this.PageScrollService.start(pageScrollInstance);
    };

	shortUrl(public_id, title) {
		return 'https://www.wittycircle.com/project/' + public_id + '/' + this.transformUrl(title);
	}

	transformUrl(url) {
		url = url.replace(/ /g, '-');
		return url;
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	transformLink(link) {
		return link.slice( (link.indexOf('/') + 2), link.length);
	}

	slideCarousel(index, type) {
		if (type === 'picture' && this.project.video)
			index = index + 1
		this.leftPixel =  (index * -480).toString() + 'px'
	}

	getDiscussionNumber(number) {
		this.d_length = number
	}

	/* SHOW MODAL */
	showMessageModal() {
        let disposable 	= this.dialogService.addDialog(MessageModalComponent, {
            profile : this.author
	    });
	}

	showLoginPopOver() {
        let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {

	    });
	}
}



