import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Rx';

import { AuthHttp } from 'ng2-bearer';

@Injectable()
export class ProjectDiscussionsService {

	constructor(private http: Http, private AuthHttp: AuthHttp) { }

	// Get all project's discussions
	getDiscussions(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/' + id + '/discussions'

		return this.AuthHttp.get(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Create new project's discussion
	createDiscussion(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/projects/' + id + '/discussions'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Update project's discussion
	updateDiscussion(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/discussions/' + id // TODO: Secure update for only creator who will be able to modify

		return this.AuthHttp.put(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Like project's discussion
	likeDiscussion(id: number, body: Object) : Observable<any> {
		const url = 'http://localhost:3000/api/discussions/' + id + '/like'

		return this.AuthHttp.post(url, body)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Unlike project's discussion
	unlikeDiscussion(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/discussions/' + id + '/like'

		return this.AuthHttp.delete(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

	// Remove discussion from project
	deleteDiscussion(id: number) : Observable<any> {
		const url = 'http://localhost:3000/api/discussions/' + id

		return this.AuthHttp.delete(url)
			.map( (res:Response) => res.json())
			.catch( (error: any) => Observable.throw(JSON.parse(JSON.stringify(error))));
	}

}



