import { Component, OnInit, Input, EventEmitter, Output, OnChanges, SimpleChanges } from '@angular/core';
import { DialogService } from "ng2-bootstrap-modal";

/* Components */
import { PopoverLoginComponent } from '../../modals/popover-login/popover-login.component';

/* Services */
import { ProjectDiscussionsService } from '../../../Services/Projects/project-discussions.service';
import { ProjectReplyDiscussionService } from '../../../Services/Projects/project-reply-discussion.service';
import { ProfilesService } from '../../../Services/Profiles/profiles.service';
import { TokenService } from '../../../Services/Authentication/token.service';
import { PicturesService } from '../../../Services/Pictures/pictures.service';

@Component({
	selector: 'app-project-discussions',
	templateUrl: './project-discussions.component.html',
	styleUrls: ['./project-discussions.component.scss']
})
export class ProjectDiscussionsComponent implements OnInit, OnChanges {

	@Input() reload 	: boolean
	@Input() project_id	: number
	@Output() emit	: EventEmitter<any> = new EventEmitter<any>()

	logged 				: boolean = false
	my_project 			: boolean = false
	my_id 				: number
	discussions			: any = []
	discussionTitle 	: string
	discussionMessage 	: string
	replyMessage 		: string
	profile 			: Object = {}

	constructor(private Discussion: ProjectDiscussionsService,
				private TokenService: TokenService,
				private ReplyService: ProjectReplyDiscussionService,
				private ProfilesService: ProfilesService,
				private dialogService: DialogService,
				private PicturesService: PicturesService) {
		if (this.TokenService.getToken()) {
			this.my_id = this.TokenService.getToken().user.id
			this.logged = true
		}
		this.my_project = true
	}

	ngOnInit() {
		this.initServices();
	}

	ngOnChanges(changes: SimpleChanges) {
		this.initServices();
	}

	initServices() {
		this.getProfile();
		this.getDiscussions();
	}

	getProfile() {
		this.ProfilesService.getProfile(this.my_id).subscribe( res => {
			this.profile = res.profile
		});
	}

	/*** DISCUSSIONS ***/
	getDiscussions() {
		this.Discussion.getDiscussions(this.project_id).subscribe( res => {
			this.discussions = res.discussions
			this.emitDiscussionNumber(this.discussions.length);
		}, error => {
			if (error)
				this.discussions = [];
		});
	}

	resetDiscussions() {
		this.getDiscussions();
		this.discussionTitle 	= ''
		this.discussionMessage 	= ''
		this.replyMessage 		= ''
	}

	addDiscussion() {
		if (this.checkLogin()) {
			const body = {
				message : this.replaceNewline(this.discussionMessage),
			}
			this.Discussion.createDiscussion(this.project_id, body).subscribe( res => {
				if (res.id) {
					this.resetDiscussions();
				}
			})
		}
	}

	loadComment(message: string) {
		this.replyMessage = message
	}

	likeDiscussion(id, liked, index) {
		if (this.checkLogin()) {
			this.Discussion.likeDiscussion(id, {}).subscribe( res => {
				if (res) {
					this.discussions[index].replies[0]
					if (liked) {
						this.discussions[index].replies[0].liked = false
						this.discussions[index].replies[0].likes.splice(0, 1);
					} else {
						this.discussions[index].replies[0].liked = true
						this.discussions[index].replies[0].likes.push({fake: 'like'});
					}
				}
			});
		}
	}

	removeDiscussion(id, index) {
		if (this.checkLogin()) {
			this.Discussion.deleteDiscussion(id).subscribe( res => {
				if (res.success)
					this.discussions.splice(index, 1);
			});
		}
	}

	/*** REPLIES ***/
	addReply(id) {
		if (this.checkLogin()) {
			this.ReplyService.createReply(id, { message: this.replaceNewline(this.replyMessage) }).subscribe( res => {
				if (res.success)
					this.resetDiscussions();
			})
		}
	}

	loadReply(message: string) {
		this.replyMessage = message;
	}

	updateReply(id) {
		if (this.checkLogin()) {
			this.ReplyService.updateReply(id, { message: this.replaceNewline(this.replyMessage) }).subscribe( res => {
				if (res.success)
					this.resetDiscussions();
			});
		}
	}

	likeReply(id, liked, index1, index2) {
		if (this.checkLogin()) {
			this.ReplyService.likeReply(id, {}).subscribe( res => {
				if (liked) {
					this.discussions[index1].replies[index2].liked = false
					this.discussions[index1].replies[index2].likes.splice(0, 1);
				} else {
					this.discussions[index1].replies[index2].liked = true
					this.discussions[index1].replies[index2].likes.push({fake: 'like'});
				}
			});
		}
	}

	removeReply(id, index1, index2) {
		if (this.checkLogin()) {
			this.ReplyService.deleteReply(id).subscribe( res => {
				if (res.success)
					this.discussions[index1].replies.splice(index2, 1);
			});
		}
	}

	emitDiscussionNumber(number) {
		this.emit.emit(number);
	}

	replaceNewline(text) {
		return text.replace(/\r?\n/g, '<br />');
	}

	checkLogin() {
		if (!this.logged) {
			let disposable 	= this.dialogService.addDialog(PopoverLoginComponent, {});
			return false
		} else
			return true
	}

	transformImage(url: string, width: number, height: number, crop: string) {
		return this.PicturesService.transformImage(url, width, height, crop);
	}

	// DEPRECATED
	// getReplies() {
	// 	this.ReplyService.getReplies(12).subscribe(res => {
	// 		console.log(res);
	// 	});
	// }
}



